import { queryClient } from '@/shared/api/query-client'
import { departmentsApi } from '@/entities/departments/api/departments.api'
import React from 'react'
import { RefreshCcw } from 'lucide-react'
import { Button } from '@/components/ui/button'

interface DepartmentPageProps {
	params: Promise<{ id: string }>
}

export default async function DepartmentPage({ params }: DepartmentPageProps) {
	const { id } = await params

	// Если данные о департаментах уже закешированы, они будут использованы
	const allDepartments = await queryClient.ensureQueryData({
		queryKey: ['departments'],
		queryFn: () => departmentsApi.getDepartments()
	})

	const department = allDepartments?.find(dept => dept.id.toString() === id)

	if (!department) {
		return (
			<div className='p-6'>
				<h1>Ошибка</h1>
				<p>Департамент не найден</p>
			</div>
		)
	}

	return (
		<div className='p-6'>
			<div className='flex flex-row justify-between'>
				<h1 className='text-4xl'>{department.name}</h1>
				<Button>
					<RefreshCcw />
				</Button>
			</div>
			<div className='mt-6 space-y-4'>
				<p>
					<span className='font-semibold'>ID:</span> {department.id}
				</p>
				<p>
					<span className='font-semibold'>Статус:</span>{' '}
					{department.isActive ? 'Активен' : 'Неактивен'}
				</p>
				<p>
					<span className='font-semibold'>Путь:</span>{' '}
					{department.path}
				</p>
				<p>
					<span className='font-semibold'>Глубина:</span>{' '}
					{department.depth}
				</p>
				<p>
					<span className='font-semibold'>Позиций:</span>{' '}
					{department.positionCount}
				</p>
				<p>
					<span className='font-semibold'>Обновлено:</span>{' '}
					{department.updatedAt.toLocaleString()}
				</p>
			</div>
		</div>
	)
}
